document.addEventListener("DOMContentLoaded", e => {
  const btn = document.querySelector("#btn")
  const petsUrl = '../dist/data/petInfo.json'
  const container = document.querySelector('.container-center')

  console.log("Test!")

  btn.addEventListener('click', reqData);
  
  function reqData() {
    fetch(petsUrl)
      .then(res => res.json())
      .then(data => adder(data))
      .catch((error) => {
        console.error(error);
      })
  }

  function adder(data) {
    console.log(data);
    const ul = document.createElement('ul');
    container.append(ul);
    data.forEach(ele => {
      const li = document.createElement('li');
      li.textContent = `${ele.name}`;
      const pic = document.createElement('img');
      pic.src = `./assets/pets/${ele.name.toLowerCase()}/${ele.photos[0]}`
      li.appendChild(pic)
      ul.append(li);
    })
  }
});
